/*Navbar Styling*/
.navbar{
    font-family: 'open-sans', sans-serif;
    overflow: hidden;
    position: fixed;
    width: 100%;
    padding: 18px 0;
    /* background: #FFCD33; */
    font-family: 'Ubuntu', 'sans-serif';
    transition: all 0.3s ease;
    z-index: 997;


    &.active{
        transition: all 0.3s ease;
        background-color: $mainColour;
    }
    &.invis{
        transition: all 0.3s ease;
        opacity: 0;
    }
    .max-width{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .logo{
        a{
            color: #FFCD33;
            font-size: 28px;
            font-weight: 600;
        }
    }
    .menu{
        overflow: hidden;
        li{
            list-style: none;
            display: inline-block;

            a{

                color: #fff;
                font-size: 15px;
                font-weight: 500;
                margin-left: 25px;
                transition: cover 0.3s ease;

            
                &:hover{
                    color: #FFCD33;
                    transition: all 0.3s ease-out;
                }
            }            
        }
        .current{
            overflow: hidden;
            a{
                color: #FFCD33;
                font-weight: 500;
                margin-left: 25px;
                transition: cover 0.3s ease;
                &:hover{
                    color: #FFCD33;
                    transition: all 0.3s ease-out;
                }
            }
        }

    }
    .menu-active{
        overflow: hidden;
        li{
            list-style: none;
            display: inline-block;
            a{
                color: #fff;
                font-size: 18px;
                font-weight: 500;
                margin-left: 25px;
                transition: cover 0.3s ease;

                &:hover{
                    color: #FFCD33;
                    transition: all 0.3s ease-out;
                }
            }
        }
    }
}

/* menu button styling */
.menu-icon{
    color: #fff;
    font-size: 23px;
    cursor: pointer;
    display: none;
}

@media (max-width: 1240px){
    .navbar .menu-active{
        overflow: hidden;
        li{
            list-style: none;
            display: inline-block;
            a{
                color: #fff;
                font-size: 18px;
                font-weight: 500;
                margin-left: 25px;
                transition: cover 0.3s ease;

                &:hover{
                    color: #FFCD33;
                    transition: all 0.3s ease-out;
                }
            }
        }
    }
}
@media (max-width: 991px){
    .max-width{
        padding: 0 50px;
    }
    .navbar .menu-active{
        li{
            list-style: none;
            display: inline-block;
            a{
                color: #fff;
                font-size: 18px;
                font-weight: 500;
                margin-left: 25px;
                transition: cover 0.3s ease;

                &:hover{
                    color: #FFCD33;
                    transition: all 0.3s ease-out;
                }
            }
        }
    }
}
@media (max-width: 947px){
    .max-width{
        padding: 0 50px;
    }
    .menu-icon{
        display: block;
        z-index: 999;
        color: #fff;
        font-size: 23px;
        cursor: pointer;
    }
    .logo{
        z-index: 999;
        a{
            color: #fff;
            font-size: 35px;
            font-weight: 600;
        }
    }
    .navbar {
        position: fixed;
        width: 100%;
        padding: 20px 0;
        /* background: #FFCD33; */
        font-family: 'Ubuntu', 'sans-serif';
        transition: all 0.3s ease;
        .menu{
            position: fixed;
            height: 100vh;
            width: 100%;
            left: -100%;
            top: 0;
            background: #111;
            text-align: center;
            padding-top: 80px;
            transition: all 0.3s ease;

            li{
                display: block;
                a{
                    color: white;
                    display: inline-block;
                    margin: 20px 0;
                    font-size: 25px;
                }
            }
        }
        .menu-active{
            z-index: 998;
            position: fixed;
            height: 100vh;
            width: 100%;
            left: 0;
            top: 0;
            background: #111;
            text-align: center;
            padding-top: 80px;
            transition: all 0.3s ease;
            li{
                display: block;
                a{
                    color: white;
                    display: inline-block;
                    margin: 20px 0;
                    font-size: 25px;
                    transition: cover 0.3s ease;
    
                    &:hover{
                        color: #FFCD33;
                        transition: all 0.3s ease-out;
                    }
                }
            }
            .current{
                display: block;
                a{
                    color: #FFCD33;
                    display: inline-block;
                    margin: 20px 0;
                    font-size: 25px;
                    transition: cover 0.3s ease;
    
                    &:hover{
                        color: #FFCD33;
                        transition: all 0.3s ease-out;
                    }
                }
            }
        }
    }
}
